import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    name: "Ali",
    users: [],
  },
  mutations: {
    setName(state, name) {
      state.name = name;
    },
    getData(state, data) {
      state.users = data;
    },
  },
  actions: {
    updateName({ commit }, name) {
      commit("setName", name);
    },
    updateData({ commit }, data) {
      commit("getData", data);
    },
  },
 
  getters: {
    getName: (state) => state.name,
    getUsers: (state) => state.users,
  },
});
