<template>
  <v-app id="app">
    <User v-if="isLoaded" :users="getUsers"/>
  </v-app>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import User from "./components/User.vue";
export default {
  name: "App",
  data: () => {
    return {
      name: "Ali",
      isLoaded:false
    };
  },
  components: {
    User,
  },
  computed: {
    ...mapGetters(["getName", "getUsers"]),
  },
  methods: {
    ...mapActions(["updateName", "updateData"]),
    updateUsername() {
      this.updateName(this.name);
    },
  },
  created() {
    const resUsers = async () => {
      const result = await fetch("https://api.github.com/users");
      const res = await result.json();
      this.isLoaded = true
      console.log(res);
      this.updateData(res);
    };
    resUsers();
  },
};
</script>

